<template>
  <AppPage>
    <audio controls autoplay loop hidden="hidden">
    <source src="http://s0.vocaroo.com/media/download_temp/Vocaroo_s0Jz5C4gpgE0.mp3" type="audio/ogg">
    </audio>

    <div class="container">
      <div  class="error-container">
        <p class="p">4</p>
        <span class="dracula">
          <div class="con">
            <div class="hair"></div>
            <div class="hair-r"></div>
            <div class="head"></div>
            <div class="eye"></div>
            <div class="eye eye-r"></div>
            <div class="mouth"></div>
            <div class="blod"></div>
            <div class="blod blod2"></div>
          </div>
        </span>
        <p class="p">4</p>
        <div class="page-ms">
          <p class="page-msg"> Oops, we couldn't find the page you are looking for </p>
        </div>
      </div>
    </div>
  </AppPage>
</template>

<script>
export default {
  name: 'NotFoundPage'
}
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

*::before,
*::after {
  content: "";
  position: absolute;
}

.error-container {
  width: 100%;
  height: auto;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 0;
}

.dracula {
  width: 230px;
  height: 300px;
  display: inline-block;
  margin: auto;
  overflow: hidden;
}

.error-container .p {
  height: 100%;
  color: #c0d7dd;
  font-size: 280px;
  margin: 50px;
  display: inline-block;
  font-family: "Anton", sans-serif;
  font-family: "Comfortaa", cursive;
  font-family: "Combo", cursive;
}

.con {
  width: 500px;
  height: 500px;
  position: relative;
  margin: 9% auto 0;
  animation: ani9 0.7s ease-in-out infinite alternate;
}

@keyframes ani9 {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(30px);
  }
}

.con > * {
  position: absolute;
  top: 0;
  left: 0;
}

.hair {
  top: -20px;
  width: 210px;
  height: 200px;
  background: #c0d7dd;
  border-radius: 0 50% 0 50%;
  transform: rotate(45deg);
  background: #33265c;
}
.hair-r {
  left: 20px;
  width: 210px;
  height: 200px;
  background: #c0d7dd;
  border-radius: 0 50% 0 50%;
  transform: rotate(45deg);
  background: #33265c;
}
.head {
  width: 200px;
  height: 200px;
  background: #c0d7dd;
  border-radius: 0 50% 0 50%;
  transform: rotate(45deg);
}
.eye {
  width: 20px;
  height: 20px;
  background: #111113;
  border-radius: 50%;
  top: 15%;
  left: 11.5%;
  transition: 0.3s linear;
}
.eye-r {
  left: 24%;
}

.mouth {
  width: 60px;
  height: 20px;
  background: #840021;
  top: 20%;
  left: 14%;
  border-radius: 50% / 0 0 100% 100%;
}
.mouth::after {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 13px solid #ffffff;
  left: 10px;
}
.mouth::before {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 13px solid #ffffff;
  left: 40px;
}

.blod {
  width: 8px;
  height: 20px;
  background: #840021;
  top: 23%;
  left: 17%;
  border-radius: 20px;
}
.blod::after {
  width: 2px;
  height: 10px;
  background: #fff;
  top: 20%;
  left: 10%;
  border-radius: 20px;
}
.blod2 {
  top: 23%;
  left: 20%;
  width: 13px;
  height: 13px;
  border-radius: 50% 50% 50% 0;
  transform: rotate(130deg);
  animation: blod 2s linear infinite;
  opacity: 0;
}
@keyframes blod {
  0% {
    opacity: 1;
  }
  100% {
    background: red;
    opacity: 0;
    top: 50%;
  }
}

/* page-ms */
.page-ms {
  transform: translateY(-50px);
}

.error-container p.page-msg {
  text-align: center;
  color: #c0d7dd;
  font-size: 30px;
  font-family: "Combo", cursive;
  margin-bottom: 20px;
}
button.go-back {
  font-size: 30px;
  font-family: "Combo", cursive;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.3s linear;
  z-index: 9;
  border-radius: 10px;
  background: #c0d7dd;
  color: #33265c;
  box-shadow: 0 0 10px 0 #c0d7dd;
  margin-top: 20px;
}
button:hover {
  box-shadow: 0 0 20px 0 #c0d7dd;
}
</style>



